	module.exports = {"data":{"markdownRemark":{"html":"<h2>Math Floor</h2>\n<p><code>Math.floor()</code> is a method of the Math standard object that rounds a given number downwards to the next integer. Take note that for negative numbers this means that the number will get rounded “away from 0” instead of to the number of smaller absolute value since <code>Math.floor()</code> returns the largest integer less than or equal to the given number.</p>\n<h3>Examples</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>Math<span class=\"token punctuation\">.</span><span class=\"token function\">floor</span><span class=\"token punctuation\">(</span><span class=\"token number\">0.9</span><span class=\"token punctuation\">)</span>  <span class=\"token comment\" spellcheck=\"true\">//  0</span>\nMath<span class=\"token punctuation\">.</span><span class=\"token function\">floor</span><span class=\"token punctuation\">(</span><span class=\"token number\">1.3</span><span class=\"token punctuation\">)</span>  <span class=\"token comment\" spellcheck=\"true\">//  1</span>\nMath<span class=\"token punctuation\">.</span><span class=\"token function\">floor</span><span class=\"token punctuation\">(</span><span class=\"token number\">0.5</span><span class=\"token punctuation\">)</span>  <span class=\"token comment\" spellcheck=\"true\">//  0</span>\nMath<span class=\"token punctuation\">.</span><span class=\"token function\">floor</span><span class=\"token punctuation\">(</span><span class=\"token operator\">-</span><span class=\"token number\">0.9</span><span class=\"token punctuation\">)</span> <span class=\"token comment\" spellcheck=\"true\">// -1</span>\nMath<span class=\"token punctuation\">.</span><span class=\"token function\">floor</span><span class=\"token punctuation\">(</span><span class=\"token operator\">-</span><span class=\"token number\">1.3</span><span class=\"token punctuation\">)</span> <span class=\"token comment\" spellcheck=\"true\">// -2</span>\n</code></pre>\n      </div>\n<h3>More Information:</h3>\n<ul>\n<li><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Math/floor\">MDN</a></li>\n<li><a href=\"https://www.w3schools.com/jsref/jsref_floor.asp\">w3schools</a></li>\n<li><a href=\"https://en.wikipedia.org/wiki/Floor_and_ceiling_functions\">Wikipedia</a></li>\n</ul>","fields":{"slug":"/javascript/standard-objects/math/math-floor/"},"frontmatter":{"title":"Math Floor"}}},"pathContext":{"slug":"/javascript/standard-objects/math/math-floor/"}}

